$translateStart: translateY(9%);
$translateEnd: translateY(-10%);

@keyframes secondCoinsAnimaiton {
  0% {
    transform: $translateStart;
  }

  100% {
    transform: $translateEnd;
  }
}

.icon {
  position: absolute;
  height: auto !important;
  animation: secondCoinsAnimaiton 3s steps(50, end) infinite alternate;
  z-index: 1;

  &__1 {
    width: 9% !important;
    right: 1%;
    top: -6%;
  }
  &__2 {
    width: 5% !important;
    right: -2%;
    bottom: 20%;
  }
  &__3 {
    width: 8.5% !important;
    right: 10%;
    bottom: 30%;
  }
  &__4 {
    width: 7% !important;
    right: 38%;
    bottom: -10%;
  }
  &__5 {
    width: 12% !important;
    right: 20.6%;
    bottom: -6%;
  }
  &__6 {
    width: 8% !important;
    bottom: -17%;
    right: 11%;
  }
  &__7 {
    width: 8.7% !important;
    right: -0.4%;
    bottom: -6%;
  }
  &__8 {
    width: 10% !important;
    top: -9%;
    right: 14%;
  }
}

.blur {
  position: absolute;
  width: 200px !important;
  height: auto !important;
  animation: secondCoinsAnimaiton 2s steps(20, end) infinite alternate;

  &__1 {
    width: 30% !important;

    left: auto;
    right: 11%;
    bottom: -45%;
    top: auto;
    z-index: 0;
  }

  &__2 {
    right: 5%;
    width: 20% !important;
    height: auto !important;
    top: 20%;
    z-index: 0;
  }

  &__3 {
    right: -5%;
    top: -24%;
    width: 22% !important;
    height: auto !important;
    z-index: 0;
  }
}
