$translateYStart: translateY(-10%);
$translateYEnd: translateY(9%);

@keyframes firstCoinsAnimaiton {
  0% {
    transform: $translateYStart;
  }

  100% {
    transform: $translateYEnd;
  }
}

@keyframes squeeze {
  0% {
    transform: scaleX(0.9) $translateYStart;
  }

  100% {
    transform: scaleX(1.03) $translateYEnd;
  }
}

@keyframes blurSqueeze {
  0% {
    transform: scaleX(1) $translateYStart;
  }

  100% {
    transform: scaleX(1.1) $translateYEnd;
  }
}

.icon {
  position: absolute;
  height: auto !important;
  animation: firstCoinsAnimaiton 3s steps(50, end) infinite alternate;

  &__1 {
    width: 4.4% !important;
    top: -6%;
    right: 26%;
  }
  &__2 {
    width: 5.3% !important;
    top: auto;
    bottom: 75%;
    right: 10%;
  }
  &__3 {
    width: 5.2% !important;
    top: auto;
    bottom: 65%;
    right: -2%;
  }
  &__4 {
    width: 3.9% !important;
    top: auto;
    bottom: 40%;
    right: 0.4%;
  }
  &__5 {
    width: 4.7% !important;
    top: auto;
    bottom: 16%;
    right: 6%;
    z-index: 1;
  }
  &__6 {
    width: 5% !important;
    top: auto;
    bottom: 10%;
    right: 16%;
  }
  &__7 {
    width: 8% !important;
    top: auto;
    bottom: 10%;
    right: 32%;
  }
  &__8 {
    animation: squeeze 3s steps(50, end) infinite alternate;
    width: 11% !important;
    top: auto;
    bottom: -5%;
    right: 45%;
    z-index: 1;
  }
}

.blur {
  animation: blurSqueeze 3s steps(20, end) infinite alternate;

  height: auto !important;
  width: 25% !important;

  position: absolute;
  top: auto;
  bottom: -27%;
  right: 37.5%;
  z-index: 0;
}
