@keyframes barMove {
  0% {
    transform: translateY(-2%);
  }

  100% {
    transform: translateY(2%);
  }
}

@keyframes lightMove {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.bar {
  height: 170% !important;
  width: auto !important;

  position: absolute;
  right: -10%;
  top: -35%;

  animation: barMove 3s steps(55, end) infinite alternate;
}

.lights {
  position: absolute;
  height: 100% !important;
  width: auto !important;

  right: 5%;
  top: 0;

  animation: lightMove 20s steps(260, end) infinite;
  opacity: 0.4;
}
