@import "sass/index";

.container {
  font-family: "Gilroy";

  position: relative;
  width: 100%;
  height: 100%;
  cursor: pointer;

  background: url("./elements/background.svg") 100% no-repeat;
  background-size: cover;
  border-radius: 30px;
  overflow: hidden;

  @media (max-width: $mobileScreen) {
    border-radius: 10px;
  }

  &:hover,
  &:focus,
  &:active {
    & .text {
      transform: scale(1.05);
    }
  }
}

@keyframes buttonAnimate {
  0% {
    transform: scale(1);
  }

  100% {
    transform: scale(1.1);
  }
}

.button {
  background: #fed61c !important;
  padding: 0.5vw 6vw;
  border-radius: 30px;

  font-weight: 700;
  font-size: 1.5vw;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  color: #032379;

  position: absolute;
  top: 70%;
  left: 4%;

  animation: buttonAnimate 1s steps(15, end) infinite alternate;
}

.text {
  font-weight: 900;
  font-size: 1.5vw;
  letter-spacing: 0.03em;
  color: #ffffff;
  width: fit-content;
  position: absolute;
  top: 54%;
  left: 4%;
  margin: 0;

  & span {
    color: #fed61c;
  }
}

.button,
.img,
.text {
  transition: all 0.4s;
}

.disable {
  & * {
    animation: none !important;
  }
}
